    position:fixed;
    text-align:center;
    top:50%;
    transform:translate(-50%, -50%);
} */

h1 {
  font-family: monospace;
}

@keyframes slide {
  0% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(25%);
  }
}
</style>
